'use client'

import { useState } from 'react'
import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import { EventInput } from '@fullcalendar/core'
import nbLocale from '@fullcalendar/core/locales/nb'

export default function Planlegging() {
  const [events, setEvents] = useState<EventInput[]>([])

  const handleDateSelect = (selectInfo: any) => {
    const title = prompt('Skriv inn aktivitetsnavn:')
    if (title) {
      const calendarApi = selectInfo.view.calendar
      calendarApi.unselect() // fjern valg etter at hendelsen er lagt til

      setEvents([...events, {
        id: createEventId(),
        title,
        start: selectInfo.startStr,
        end: selectInfo.endStr,
      }])
    }
  }

  const handleEventDrop = (dropInfo: any) => {
    const updatedEvents = events.map(event => {
      if (event.id === dropInfo.event.id) {
        return {
          ...event,
          start: dropInfo.event.startStr,
          end: dropInfo.event.endStr,
        }
      }
      return event
    })
    setEvents(updatedEvents)
  }

  const handleEventChange = (changeInfo: any) => {
    // HÃ¥ndter endringer i hendelser
    const updatedEvents = events.map(event => {
      if (event.id === changeInfo.event.id) {
        return {
          ...event,
          title: changeInfo.event.title,
          start: changeInfo.event.startStr,
          end: changeInfo.event.endStr,
        }
      }
      return event
    })
    setEvents(updatedEvents)
  }

  const createEventId = () => {
    return String(Date.now())
  }

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-6">Planlegging</h1>
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-2 bg-white rounded-lg shadow-lg p-4">
          <FullCalendar
            plugins={[dayGridPlugin, interactionPlugin]}
            initialView="dayGridMonth"
            locale={nbLocale}
            selectable={true}
            editable={true}
            events={events}
            select={handleDateSelect}
            eventDrop={handleEventDrop}
            eventChange={handleEventChange}
            headerToolbar={{
              left: 'prev,next today',
              center: 'title',
              right: 'dayGridMonth,dayGridWeek'
            }}
            height="auto"
            firstDay={1}
          />
        </div>
        
        <div className="bg-white rounded-lg shadow-lg p-4">
          <h2 className="text-xl font-semibold mb-4">Planlagt vedlikehold</h2>
          <div className="space-y-4">
            {events.sort((a, b) => 
              new Date(a.start as string).getTime() - new Date(b.start as string).getTime()
            ).map((event) => (
              <div key={event.id} className="border-b pb-3">
                <div className="font-medium text-blue-600">{event.title}</div>
                <div className="text-sm text-gray-600">
                  {new Date(event.start as string).toLocaleDateString('nb-NO', {
                    weekday: 'long',
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric'
                  })}
                </div>
                <button 
                  onClick={() => {
                    setEvents(events.filter(e => e.id !== event.id))
                  }}
                  className="text-sm text-red-500 hover:text-red-700 mt-1"
                >
                  Slett
                </button>
              </div>
            ))}
            {events.length === 0 && (
              <p className="text-gray-500 italic">Ingen planlagte vedlikeholdsoppgaver</p>
            )}
          </div>
        </div>
      </div>
    </div>
  )
} 